.slick-prev:before {
  content: '\e805';
}
.slick-next:before {
  content: '\e806';
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-current,
.slick-slide,
.slick-active {
