}
.open {
  transform: translateX(100vw);
  transition: all 0.35s cubic-bezier(0.72, -0.04, 0.14, 1.03);
  position: relative;
  z-index: 100;
}
@media only screen and (min-width: 992px) {
  .open {
    display: none;
  }
}
.overlay {
  padding-top: 10px;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 50px;
  left: -100vw;
  background: #95c557;
  overflow-y: scroll;
  transition: all 0.3s cubic-bezier(0.72, -0.04, 0.14, 1.03);
}
@media only screen and (min-width: 992px) {
  .overlay {
    padding-top: 0;
    position: inherit;
    width: inherit;
    height: inherit;
    left: inherit;
    top: inherit;
    background: inherit;
    overflow-y: inherit;
  }
}
.overlay::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: transparent;
}
.overlay:-webkit-scrollbar {
  width: 6px;
  background-color: transparent;
}
.overlay:-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #11569d;
}
.burger {
  display: block;
  position: relative;
  width: 50px;
  height: 50px;
  transition: all 0.6s ease-in-out;
  background: transparent;
  border: none;
  outline: none;
  z-index: 1000;
  order: 0;
}
.burger:hover {
  cursor: pointer;
}
@media only screen and (min-width: 992px) {
  .burger {
    display: none;
  }
}
.burger i {
  display: block;
  width: 40px;
  height: 2px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before,
.burger i:after {
  content: '';
  position: absolute;
  height: 2px;
  background: #fff;
  width: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger-white i {
  background: #fff;
}
.burger-white i:before,
.burger-white i:after {
  background: #fff;
}
.burger i:before {
  margin-top: -12px;
}
.burger i:after {
  margin-top: 12px;
}
.burger:hover i:before {
  margin-top: -14px;
}
.burger:hover i:after {
  margin-top: 14px;
}
.burger.active i {
  background: transparent;
}
.burger.active i:before,
.burger.active i:after {
  left: -5px;
  width: 50px;
}
.burger.active i:before {
  transform: rotate(45deg);
  top: 12px;
}
.burger.active i:after {
  transform: rotate(-45deg);
  top: -12px;
}
.burger.active:hover i:before,
.burger.active:hover i:after {
  width: 60px;
  margin: 0;
  left: -10px;
  top: 0;
}
